import { css, keyframes } from 'glamor';
import React from 'react';
import { chat, isInViewport, scrollByPage } from '../index';
import { ChatBubble } from "../components/ChatBubble";
import { getImg } from '../mabinogi3th_characters';
import pageDatas from '../CharacterPage_data.json'

export class CharacterPage extends React.Component {

  previousSelectCh = -1

  constructor(props) {
    super(props)
    this.state =
    {
      play: false,
      selectCh: 0,
    }
    this.chatRandomPosition();
  }

  chatRandomPosition() {
    const shuffle = (array) => {
      for (let i = array.length - 1; i > 0; i--) {
        let j = Math.floor(Math.random() * (i + 1));
        [array[i], array[j]] = [array[j], array[i]];
      }
      return array.slice();
    };
    const createRandomArray = (a, b, step = 1) => {
      const min = (a >= b) ? b : a;
      const max = (min === b) ? a : b;
      let _return = [];
      for (let i = min; i < max; i += step) {
        _return.push(i);
      }
      return shuffle(_return);
    };
    let left = createRandomArray(-5, 2, 2);
    let bottom = createRandomArray(2, 10, 2);
    const getValue = (of = [], index) => {
      const i = index % of.length; //循環取得
      const val = of[i];
      return val + ((Math.random() * 0.5) * ((Math.random > 0.5) ? -1 : 1)); //取得±0.5
    };
    let ch_chats = [];
    pageDatas[this.state.selectCh].ch_chats.forEach((e, i) => {
      ch_chats.push(chat(e.str, e._name, e._url))
    })
    this.ch_chats = []
    ch_chats.forEach((e, i) => {
      let t = { chat: e, pos: { l: 0, b: 0 } }
      t.pos.l = getValue(left, i)
      t.pos.b = getValue(bottom, i)
      this.ch_chats.push(t)
    })
  }

  componentDidMount() {
    window.addEventListener("scroll", s => {
      const element = document.getElementById('characterpage');
      if (element === undefined || element === null)
        return;
      if (isInViewport(element)) {
        if (!this.state.play) this.setState({ play: true })
      }
      else {
        if (this.state.play) this.setState({ play: false })
      }
    });
    this.previousSelectCh = this.state.selectCh
  }

  componentDidUpdate() {
    this.previousSelectCh = this.state.selectCh
  }

  render() {

    const CurrentCharacter = pageDatas[this.state.selectCh]

    if (this.previousSelectCh !== this.state.selectCh) { this.chatRandomPosition() }

    let ChUrl = []
    for (let index = 0; index < 9; index++) {
      try {
        const element = require('../characters/' + CurrentCharacter.imgID + '_' + index + '.png');
        ChUrl.push(element.default)
      } catch (error) {

      }
    }

    const height = `${100 / 3}%`
    const page = css({
      flex: 1,
      display: "flex",
      width: '100%',
      height: '100%',
      overflow: 'hidden'
    });
    const Container = css({
      width: '100%',
      height: '100%',
    })
    const CenterCharacterAnim = keyframes({
      '0%': { filter: 'blur(10px)', opacity: 0 },
      '100%': { filter: 'blur(0px)', opacity: 1 }
    })
    const CenterCharacterAnim_out = keyframes({
      '0%': { filter: 'blur(0px)', opacity: 1 },
      '100%': { filter: 'blur(10px)', opacity: 0 },
    })
    const CenterCharacter = css({
      height: `${100 / 3}%`,
      width: '100%',
      backgroundRepeat: 'no-repeat',
      backgroundImage: `url(${ChUrl[0]})`,
      backgroundSize: `${50 + CurrentCharacter.choffset.center.s}%`,
      backgroundPositionX: `${50 + CurrentCharacter.choffset.center.x}%`,
      backgroundPositionY: `${5 + CurrentCharacter.choffset.center.y}em`,
      position: 'absolute',
      animation: (this.state.play) ? `${CenterCharacterAnim} 1s forwards` : `${CenterCharacterAnim_out} 1s forwards`
    })
    const CloseUp_Fading = keyframes({
      '0%': { opacity: 0 },
      '30%': { opacity: 0.5 },
      '70%': { opacity: 0.5 },
      '100%': { opacity: 0 }
    })
    const CloseUp_Out = keyframes({
      '0%': { opacity: 0.5 },
      '100%': { opacity: 0 }
    })

    //3秒經過0-100%
    //如果要更替三張圖片，則，3秒要經過一次照片
    const definedKey = (target, persent, index) => {
      Object.defineProperty(target, `${persent}%`, {
        value: { backgroundImage: `url("${ChUrl[index]}")` },
        enumerable: true,
        configurable: true,
      });
    }
    const url_persent = 100 / ChUrl.length
    let CloseUp_1_backgrounds = {}
    for (let index = 0; index < ChUrl.length; index++) {
      //總共會經過兩次，因為只有兩張
      const currentPersent = url_persent * index;
      definedKey(CloseUp_1_backgrounds, currentPersent, index)
      //為了達到Static的效果，前一格需要還是前一張
      if (index !== 0) {
        definedKey(CloseUp_1_backgrounds, currentPersent - 0.01, index - 1)
      }
      //為了達到Static的效果，最後一格要是這一張
      if (index === ChUrl.length-1) {
        definedKey(CloseUp_1_backgrounds, 100, index)
      }
    }
    const CloseUp_1_Backgrounds_Keyframe = keyframes(CloseUp_1_backgrounds);
    const CloseUp_1_Anim = (this.state.play) ? `${CloseUp_Fading} 3s ease infinite` : `${CloseUp_Out} 1s forwards`
    const CloseUp_1 = css({
      height: height,
      width: '100%',
      backgroundRepeat: 'no-repeat',
      backgroundSize: `${100 + CurrentCharacter.choffset.closeup1.s}%`,
      backgroundPositionX: `${-14 + CurrentCharacter.choffset.closeup1.x}em`,
      backgroundPositionY: `${1 + CurrentCharacter.choffset.closeup1.y}em`,
      backgroundImage: `url(${ChUrl[0]})`,
      position: 'absolute',
      opacity: 0,
      WebkitMaskImage: 'radial-gradient(closest-corner at 25% 46%, black 51%, transparent 93%)',
      animation: CloseUp_1_Anim,
      '::after': {
        content: '\'\'',
        position: 'absolute',
        height: '100%',
        width: '100%',
        backgroundRepeat: 'no-repeat',
        backgroundSize: `${100 + CurrentCharacter.choffset.closeup1.s}%`,
        backgroundPositionX: `${-14 + CurrentCharacter.choffset.closeup1.x}em`,
        backgroundPositionY: `${1 + CurrentCharacter.choffset.closeup1.y}em`,
        backgroundImage: `url(${ChUrl[1]})`,
        opacity: 1,
        animation: `${CloseUp_1_Backgrounds_Keyframe} ${ChUrl.length * 3}s infinite`
      }
    })
    const CloseUp_2_anim = keyframes({
      '0%': { backgroundPositionY: `${-100 + CurrentCharacter.choffset.closeup2anim.y1}em` },
      '100%': { backgroundPositionY: `${-10 + CurrentCharacter.choffset.closeup2anim.y2}em` }
    })
    const CloseUp_2_Anim = `${CloseUp_2_anim} 20s ease-in-out infinite, ${CloseUp_Fading} 20s ease-in-out infinite alternate ${(!this.state.play) ? `, ${CloseUp_Out} 1s forwards` : ``}`
    const CloseUp_2 = css({
      height: height,
      width: '100%',
      backgroundRepeat: 'no-repeat',
      backgroundImage: `url(${ChUrl[0]})`,
      backgroundSize: `${60 + CurrentCharacter.choffset.closeup2.s}%`,
      backgroundPositionX: `${120 + CurrentCharacter.choffset.closeup2.x}%`,
      backgroundPositionY: `${-75 + CurrentCharacter.choffset.closeup2.y}em`,
      position: 'absolute',
      opacity: 0,
      WebkitMaskImage: 'linear-gradient(0, transparent,white,white, white, white,white,transparent)',
      animation: CloseUp_2_Anim
    })

    const title = CurrentCharacter.chname
    let TitleSpan = []
    if (title !== undefined) {
      for (let index = 0; index < title.length; index++) {
        const style = {
          display: "inline-block",
          transform: "scale(0, 0)",
          animation: (this.state.play) ?
            `ScaleIn-text 0.3s cubic-bezier(0.04, 0.83, 0.35, 1.58) ${(index * 0.1)}s forwards` :
            `WaveOut 0.3s ease forwards`
        };
        const text = title.slice(index, index + 1);
        TitleSpan.push(<span style={style} key={"_title" + index + "_" + text}>{text}</span>);
      }
    }

    const infocontainer = css({
      width: '40%',
      top: '77%',
      right: 0,
      position: 'absolute',
      display: 'flex',
      alignItems: 'flex-start',
      justifyContent: 'flex-start',
      flexDirection: 'column',
      alignContent: 'flex-start',
      flexWrap: 'wrap'
    })
    const chatContainer = css({
      width: '90%',
      display: 'flex',
      placeContent: 'flex-end center',
      alignItems: 'flex-end',
      justifyContent: 'flex-end',
      alignContent: 'flex-end',
      flexDirection: 'row',
    });
    const Illsutrator = css({
      color: 'white',
      fontSize: '0.2em',
      letterSpacing: '0.1em',
      lineHeight: '9.5em',
      opacity: 0,
      textShadow: '-1.5px 1px 2px #ce8a8a',
      marginLeft: '1.1em'
    })

    const ControllerContainer = css({
      color: 'white',
      fontSize: '0.2em',
      letterSpacing: '0.1em',
      lineHeight: '9.5em',
      textShadow: '-1.5px 1px 2px #ce8a8a',
      marginLeft: '1.1em',
      position: 'absolute',
      display: 'flex',
      right: '5em',
      bottom: '5em'
    })

    const ControllerDisplay = css({
      marginLeft: '3em',
      '::before': {
        backgroundColor: 'white',
        content: '\'\'',
        width: '0',
        height: '5px',
        position: 'absolute',
        top: '24px',
        borderRadius: '50em',
        boxShadow: '-1.5px 1px 2px #ce8a8a',
        transition: '0.5s cubic-bezier(0, 0, 0, 0.99)'
      },
      ':hover::before': {
        width: '100%',
        cursor: `url(${require('../img/gui/cursors/select.png').default}), auto`
      },
      ':hover': {
        cursor: `url(${require('../img/gui/cursors/select.png').default}), auto`
      }
    })

    const ControllerDisplay_Select_an = keyframes({
      '0%': { transform: 'scale(1,1)', marginLeft: '3em', marginRight: '3em' },
      '100%': { transform: 'scale(1.5,1.5)', marginLeft: '6em', marginRight: '6em' },
    })

    const ControllerDisplay_unSelect_an = keyframes({
      '0%': { transform: 'scale(1.5,1.5)', marginLeft: '6em', marginRight: '6em' },
      '100%': { transform: 'scale(1,1)', marginLeft: '3em', marginRight: '3em' },
    })

    const ControllerDisplay_Select = css({
      animation: `${ControllerDisplay_Select_an} 0.3s cubic-bezier(0, 0, 0, 0.99) 0s forwards`
    })
    const ControllerDisplay_UnSelect = css({
      animation: `${ControllerDisplay_unSelect_an} 0.3s cubic-bezier(0, 0, 0, 0.99) 0s forwards`
    })

    let chats = []
    for (let index = 0; index < this.ch_chats.length; index++) {
      const chat = this.ch_chats[index].chat;
      const pos = this.ch_chats[index].pos;
      chats.push(
        <ChatBubble
          enable={this.state.play}
          left={pos.l}
          bottom={pos.b}
          key={index + chat.str + chat.name + chat.url}
          name={chat.name}
          sticker={getImg(chat.url, 0).url}
        >
          {chat.str}
        </ChatBubble>
      );
    }

    const changeCharacter = (index) => {
      this.setState({ selectCh: index })
    }

    let controllers = []
    for (let index = 0; index < pageDatas.length; index++) {
      const element = pageDatas[index];
      controllers.push(
        <div
          className={`${ControllerDisplay} ${(this.state.selectCh === index) ? ControllerDisplay_Select : ControllerDisplay_UnSelect}`}
          key={element.chname}
          onClick={() => { changeCharacter(index) }}>
          {element.chname}
        </div>
      )
    }

    return (
      <div {...page} id='characterpage'>
        <div {...Container}>
          <div {...CloseUp_1}
            key={"CloseUp_1" + Date.now()}></div>
          <div {...CloseUp_2}
            key={"CloseUp_2" + Date.now()}></div>
          <div {...CenterCharacter}
            key={"CenterCharacter" + Date.now()}></div>
        </div>
        <div className={infocontainer}>
          <div className={`${Illsutrator} ${(this.state.play) ? 'infocontentIn' : 'infocontentOut'}`}
            key={"illustrator" + Date.now()}
            style={{
              animationDelay: '0s'
            }}>
            Illustrator：{CurrentCharacter.illust}
          </div>
          <div className={'infoTitle'} style={{
            display: 'flex',
            justifyContent: 'flex-start',
            marginBottom: '0'
          }}>
            {TitleSpan}
          </div>
          <div
            key={"infocontent" + Date.now()}
            className={`infocontent ${(this.state.play) ? 'infocontentIn' : 'infocontentOut'}`}
            style={{
              animationDelay: '0.5s',
              marginLeft: '0.2em',
              width: "80%",
              marginBottom: '2em'
            }}
            dangerouslySetInnerHTML={{ __html: CurrentCharacter.info }}
          >
          </div>
          <div className={chatContainer}>
            {chats}
          </div>
        </div>
        <div className={ControllerContainer}>
          {controllers}
        </div>
      </div>
    )
  }
}
